  sendData_elButtonCopy_to_checkin = () => {
    const dataSheet = this.context.appActions.getDataSheet('checkin');
  
    let row = this.props.dataSheetRow || {
      "last": this.props.last,
      "dept": this.props.dept,
      "first": this.props.first,
    };
    row = { ...row, 
      datetime: (this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_date'] : ''),
      first: this.props.dataSheetRow.first,
      dept: this.props.dataSheetRow.dept,
      checkedin: (this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_out'] : ''),
      last: this.props.dataSheetRow.last,
    };
    if (this.props.dataSheetId === dataSheet.id) {
      return this.context.appActions.updateInDataSheet('checkin', row);
    } else {
      return this.context.appActions.addToDataSheet('checkin', row);
    }
  }
  
  
  sendData_elButton_to_checkin = () => {
    const dataSheet = this.context.appActions.getDataSheet('checkin');
  
    let row = this.props.dataSheetRow || {
      "last": this.props.last,
      "dept": this.props.dept,
      "first": this.props.first,
    };
    row = { ...row, 
      datetime: (this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_date'] : ''),
      id: this.props.dataSheetRow.EmployeeID,
      first: this.context.locStrings.component1_text_946796,
      dept: this.context.locStrings.user_lastcopy_172836,
      checkedin: (this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_checked'] : ''),
      last: this.context.locStrings.component1_text_964739,
    };
    return this.context.appActions.addToDataSheet('checkin', row);
  }
  
  
