import React, { Component } from 'react';
import './App.css';
import ScreenContext from './ScreenContext';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class Guest extends Component {

  static contextType = ScreenContext;

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate(prevProps, prevState) {
  }

  textInputChanged_elTxtFirstName = (event) => {
    this.setState({txtFirstName: event.target.value});
  }
  
  getValue_elTxtFirstName = () => {
    return this.state.txtFirstName ||'';
  }
  
  textInputChanged_elTxtLastName = (event) => {
    this.setState({txtLastName: event.target.value});
  }
  
  getValue_elTxtLastName = () => {
    return this.state.txtLastName ||'';
  }
  
  onClick_elButton = async () => {
    let newVal = "button";
    
    let transformValue = (input) => {
      // This function modifies the value written to the target data slot.
      // There is a variable named 'input' that provides the input value.
      var d = new Date();
      var n = d.toLocaleDateString();
      //this.context.appActions.updateDataSlot('ds_date', n);
      n = n.replace( /,/g, "" );
      input = n;
      return input;}
    newVal = transformValue(newVal);
    
    this.context.appActions.updateDataSlot('ds_date', newVal);
  
    await this.sendData_elButton_to_aquaeemps();
  
    window.setTimeout(function(){window.location.href=window.location.href},2000);
  
    // Go to screen 'wait'
    this.context.appActions.goToScreen('wait', this.context.baseScreenId, { transitionId: 'fadeIn' });
  
  }
  
  
  sendData_elButton_to_aquaeemps = () => {
    const dataSheet = this.context.appActions.getDataSheet('aquaeemps');
  
    let row = this.props.dataSheetRow || {
    };
    row = { ...row, 
      first: this.getValue_elTxtFirstName(),
      last: this.getValue_elTxtLastName(),
      dept: (this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_guest'] : ''),
    };
    if (this.props.dataSheetId === dataSheet.id) {
      return this.context.appActions.updateInDataSheet('aquaeemps', row);
    } else {
      return this.context.appActions.addToDataSheet('aquaeemps', row);
    }
  }
  
  
  render() {
    
    const style_elTxtFirstName = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const style_elTxtLastName = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const style_elButton = {
      display: 'block',
      fontSize: 12.0,
      color: 'white',
      textAlign: 'center',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    let transformPropValue_datetime = (input) => {
      var d = new Date();
      var n = d.toLocaleString();
      //this.context.appActions.updateDataSlot('ds_date', n);
      n = n.replace( /,/g, "" );
      input = n;
      return input;
    }
    
    const value_datetime = ((val) => {
      // make sure value is in string format
      if (val instanceof String ||typeof val === 'string') return val;
      else {
        try {
          return JSON.stringify(val);
        }
        catch(e) {
          return val.toString();
        }
      }
    })(transformPropValue_datetime((this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_date'] : '')));
    
    const style_elDatetime = {
      color: 'rgba(0, 0, 0, 0.8500)',
      textAlign: 'left',
     };
    const style_elDatetime_outer = {
      display: 'none',
     };
    
    return (
      <div className="Guest appBg">
        <div className="layoutFlow">
          <div className="elTxtFirstName">
            <input className="baseFont" style={style_elTxtFirstName} type="text" placeholder={this.context.locStrings.guest_field_451323} onChange={this.textInputChanged_elTxtFirstName} value={this.getValue_elTxtFirstName()}  />
          </div>
          
          <div className="elTxtLastName">
            <input className="baseFont" style={style_elTxtLastName} type="text" placeholder={this.context.locStrings.guest_field2_327248} onChange={this.textInputChanged_elTxtLastName} value={this.getValue_elTxtLastName()}  />
          </div>
          
          <div className="elButton">
            <Button className="systemFontRegular" style={style_elButton}  color="accent" onClick={this.onClick_elButton} >
              {this.context.locStrings.user2_button_236176}
            </Button>
          </div>
          
          <div className="flowRow flowRow_Guest_elDatetime_526810">
          <div className="elDatetime" style={style_elDatetime_outer}>
            <div className="baseFont" style={style_elDatetime}>
              <div>{value_datetime}</div>
            </div>
          </div>
          
          </div>
        </div>
        
      </div>
    )
  }
  
}
