import React, { Component } from 'react';
import LocalizedStrings from 'react-localization';
// eslint-disable-next-line
import { Route, Switch, Redirect, withRouter } from 'react-router-dom';
// eslint-disable-next-line
import * as util from 'util';
import './App.css';
import ScreenContext from './ScreenContext.js';
import WaitScreen from './WaitScreen.js';
import Roll2Screen from './Roll2Screen.js';
import Screen4 from './Screen4.js';
import GuestFormScreen from './GuestFormScreen.js';
import RollScreen from './RollScreen.js';
import FirstScreen from './FirstScreen.js';
import SecondScreen from './SecondScreen.js';
import StartScreen from './StartScreen.js';
import DataSheet_aquaeemps from './DataSheet_aquaeemps.js';
import DataSheet_checkin from './DataSheet_checkin.js';
import DataSheet_aquaroll from './DataSheet_aquaroll.js';
import DataSheet_localizationSheet from './DataSheet_localizationSheet.js';


class App extends Component {
  constructor(props) {
    super(props);

    this.dataSheets = {};
    this.dataSheets['aquaeemps'] = new DataSheet_aquaeemps('aquaeemps', this.dataSheetDidUpdate);
    this.dataSheets['checkin'] = new DataSheet_checkin('checkin', this.dataSheetDidUpdate);
    this.dataSheets['aquaroll'] = new DataSheet_aquaroll('aquaroll', this.dataSheetDidUpdate);
    this.dataSheets['localizationSheet'] = new DataSheet_localizationSheet('localizationSheet', this.dataSheetDidUpdate);
    this.dataSheetLoaded = {};

    this.dataSlots = {};
    this.dataSlots['ds_activeLang'] = "en";
    this.dataSlots['ds_filter'] = localStorage.getItem('ds_filter') || "";
    this.dataSlots['ds_date'] = "";
    this.dataSlots['ds_first'] = "";
    this.dataSlots['ds_last'] = "";
    this.dataSlots['ds_here'] = "Yes";
    this.dataSlots['ds_checked'] = localStorage.getItem('ds_checked') || "Yes";
    this.dataSlots['ds_out'] = "No";
    this.dataSlots['ds_guest'] = localStorage.getItem('ds_guest') || "Guest";
    this.dataSlots['ds_out_roll'] = "Delete";
    this.dataSlots['ds_dept'] = "";
    this.dataSlots['ds_roll'] = "";
    this.dataSlots['ds_roll1'] = localStorage.getItem('ds_roll1') || "Here";
    this.dataSlots['ds_roll2'] = localStorage.getItem('ds_roll2') || "Wait";

    this.updateLocalizationFromDataSheet(this.dataSheets['localizationSheet']);


    this.serviceOptions_aquaeemps = {
      dataSlots: this.dataSlots,
      servicePath: "",
      query: "",
    };
    this.dataSheets['aquaeemps'].appActions = this;
    this.dataSheets['aquaeemps'].dataSlots = this.dataSlots;
    
    this.serviceOptions_checkin = {
      dataSlots: this.dataSlots,
      servicePath: "",
      query: "",
    };
    this.dataSheets['checkin'].appActions = this;
    this.dataSheets['checkin'].dataSlots = this.dataSlots;
    
    this.serviceOptions_aquaroll = {
      dataSlots: this.dataSlots,
      servicePath: "",
      query: "",
    };
    this.dataSheets['aquaroll'].appActions = this;
    this.dataSheets['aquaroll'].dataSlots = this.dataSlots;
    

    this.state = {
      screenTransitionForward: true,
    }

  }

  windowDidResize = () => {
    let w = window.innerWidth;
    let formatId;
    if (w < 576) formatId = 'narrow-phone';
    else if (w < 768) formatId = 'wide-phone';
    else if (w < 1024) formatId = 'narrow-tablet';
    else formatId = 'wide-tablet';
    if (formatId !== this.state.screenFormatId) {
      this.setState({screenFormatId: formatId});
    }
  }

  componentDidMount() {
    this.windowDidResize();
    window.addEventListener('resize', this.windowDidResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.windowDidResize);
  }

  isLoading() {
    return this.state.loading;
  }

  goToScreen = (screenId, baseScreenId, props) => {
    // Implemented with React Router's 'history' object.
    // 'baseScreenId' is set when inside a container such as a tab bar.
    let path = '/'+screenId;
    if (baseScreenId && baseScreenId.length > 0) {
      path = '/'+baseScreenId+path;
      props = {};
    }
    this.props.history.push(path, {...props});
    window.scrollTo(0, 0);
  }

  goBack = () => {
    // Implemented with React Router's 'history' object.
    this.props.history.goBack();
  }

  getDataSheet = (sheetId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    return this.dataSheets[sheetId];
  }

  addToDataSheet = (sheetId, newRow, actionId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    let sheet = this.dataSheets[sheetId];
    if (sheet && newRow) {
      let promise = sheet.addItem(newRow, this['serviceOptions_'+sheetId] || {});
      this.setState({});
      return promise;
    }
  }

  updateInDataSheet = (sheetId, row, actionId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    let sheet = this.dataSheets[sheetId];
    if (sheet && row) {
      let promise = sheet.replaceItemByKey(row.key, row, this['serviceOptions_'+sheetId] || {});
      this.setState({});
      return promise;
    }
  }

  removeFromDataSheet = (sheetId, row) => {
    let sheet = this.dataSheets[sheetId];
    if (sheet && row) {
      let promise = sheet.removeItem(row, this['serviceOptions_'+sheetId] || {});
      this.setState({});
      return promise;
    }
  }

  updateDataSlot = (slotId, value, actionId) => {
    // This method is the default implementation and could be customized by a state management plugin.
    if (value === this.dataSlots[slotId])
      return;

    this.dataSlots[slotId] = value;

    if (slotId === 'ds_activeLang') {
      this.locStrings.setLanguage(value);
    }
    if (slotId === 'ds_filter') {
      localStorage.setItem('ds_filter', value);
    }
    if (slotId === 'ds_checked') {
      localStorage.setItem('ds_checked', value);
    }
    if (slotId === 'ds_guest') {
      localStorage.setItem('ds_guest', value);
    }
    if (slotId === 'ds_roll1') {
      localStorage.setItem('ds_roll1', value);
    }
    if (slotId === 'ds_roll2') {
      localStorage.setItem('ds_roll2', value);
    }
    this.setState({});
  }

  dataSheetDidUpdate = (dataSheet) => {
    // This method is the default implementation and could be customized by a state management plugin.
    this.setState({});
  }

  updateLocalizationFromDataSheet = (dataSheet) => {
    const stringsObj = dataSheet.getStringsByLanguage();
    if (stringsObj && Object.keys(stringsObj).length > 0) {
      this.locStrings = new LocalizedStrings(stringsObj);
    } else {
      this.locStrings = new LocalizedStrings({en: {}});
    }
    this.locStrings.setLanguage(this.dataSlots['ds_activeLang']);
  }

  loadData_jsonsrc1 = (dataSheet, options, firstLoad) => {
    // This method was written by data plugin 'Generic JSON'.
    this.setState({loading: true});
    
    // clear any placeholder data before load
    if (firstLoad) {
    	dataSheet.items = [];
    }
    
    const fetchComplete = (err) => {
      if (err) {
        // This error handling comes from React Studio
        // and currently doesn't do anything useful.
        console.error('** Web service load failed: ', err);
      } else {
      }
      this.setState({loading: false});
    }
    
    const url = dataSheet.urlFromOptions(options);  // this method was written by the web service plugin
    
    const fetchOpts = {
      method: 'GET',
      headers: {},
    };
    
    fetch(url, fetchOpts)
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Server error: "+response.status);
        }
        return response.json();
      })
      .then((json) => {
        dataSheet.loadFromJson(json);
        fetchComplete(null, options);
      })
      .catch((exc) => {
        fetchComplete(exc, options);
      });
  }
  loadData_jsonsrc2 = (dataSheet, options, firstLoad) => {
    // This method was written by data plugin 'Generic JSON'.
    this.setState({loading: true});
    
    // clear any placeholder data before load
    if (firstLoad) {
    	dataSheet.items = [];
    }
    
    const fetchComplete = (err) => {
      if (err) {
        // This error handling comes from React Studio
        // and currently doesn't do anything useful.
        console.error('** Web service load failed: ', err);
      } else {
      }
      this.setState({loading: false});
    }
    
    const url = dataSheet.urlFromOptions(options);  // this method was written by the web service plugin
    
    const fetchOpts = {
      method: 'GET',
      headers: {},
    };
    
    fetch(url, fetchOpts)
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Server error: "+response.status);
        }
        return response.json();
      })
      .then((json) => {
        dataSheet.loadFromJson(json);
        fetchComplete(null, options);
      })
      .catch((exc) => {
        fetchComplete(exc, options);
      });
  }
  loadData_jsonsrc3 = (dataSheet, options, firstLoad) => {
    // This method was written by data plugin 'Generic JSON'.
    this.setState({loading: true});
    
    // clear any placeholder data before load
    if (firstLoad) {
    	dataSheet.items = [];
    }
    
    const fetchComplete = (err) => {
      if (err) {
        // This error handling comes from React Studio
        // and currently doesn't do anything useful.
        console.error('** Web service load failed: ', err);
      } else {
      }
      this.setState({loading: false});
    }
    
    const url = dataSheet.urlFromOptions(options);  // this method was written by the web service plugin
    
    const fetchOpts = {
      method: 'GET',
      headers: {},
    };
    
    fetch(url, fetchOpts)
      .then((response) => {
        if (response.status >= 400) {
          throw new Error("Server error: "+response.status);
        }
        return response.json();
      })
      .then((json) => {
        dataSheet.loadFromJson(json);
        fetchComplete(null, options);
      })
      .catch((exc) => {
        fetchComplete(exc, options);
      });
  }

  createImageUrlFromProp = (prop) => {
    if (prop instanceof Object) {
      if (prop.type != null && prop.type === 'image' && prop.path != null) {
        return "(null)"+prop.path;
      }
    }
    return prop;
  }

  render() {
    const makeScreen = (screenId, baseProps, atTop, forward, subpathId) => {
      let screenCtxProps = {
        ...baseProps,
        atTopOfScreenStack: atTop,
        transitionForward: forward,
        appActions: this,
        dataSheets: this.dataSheets,
        locStrings: this.locStrings,
        deviceInfo: {
          screenFormatId: this.state.screenFormatId
        },
        'ds_activeLang': this.dataSlots['ds_activeLang'],
        'ds_filter': this.dataSlots['ds_filter'],
        'ds_date': this.dataSlots['ds_date'],
        'ds_first': this.dataSlots['ds_first'],
        'ds_last': this.dataSlots['ds_last'],
        'ds_here': this.dataSlots['ds_here'],
        'ds_checked': this.dataSlots['ds_checked'],
        'ds_out': this.dataSlots['ds_out'],
        'ds_guest': this.dataSlots['ds_guest'],
        'ds_out_roll': this.dataSlots['ds_out_roll'],
        'ds_dept': this.dataSlots['ds_dept'],
        'ds_roll': this.dataSlots['ds_roll'],
        'ds_roll1': this.dataSlots['ds_roll1'],
        'ds_roll2': this.dataSlots['ds_roll2'],
      };
      let screen;
      switch (screenId) {
        case 'wait':
          screen = <WaitScreen {...baseProps} />;
          break;
        case 'roll2':
          screen = <Roll2Screen {...baseProps} />;
          break;
        case 'screen4':
          screen = <Screen4 {...baseProps} />;
          break;
        case 'guestForm':
          screen = <GuestFormScreen {...baseProps} />;
          break;
        case 'roll':
          screen = <RollScreen {...baseProps} />;
          break;
        case 'first':
          screen = <FirstScreen {...baseProps} />;
          break;
        case 'second':
          screen = <SecondScreen {...baseProps} />;
          break;
        case 'start':
          screen = <StartScreen {...baseProps} />;
          break;
        default:
          screen = null;
      }
      if (screen) {
        return <ScreenContext.Provider value={screenCtxProps}>{screen}</ScreenContext.Provider>;
      }
    }

    return (
      <div className="App">
        <Switch>
          <Route path="/" render={(props) => makeScreen('start', props.location.state, true, true)} exact />
          <Route path="/wait" render={(props) => {
            return makeScreen('wait', props.location.state, true, true);
          }} />
          <Route path="/roll2" render={(props) => {
            return makeScreen('roll2', props.location.state, true, true);
          }} />
          <Route path="/screen4" render={(props) => {
            return makeScreen('screen4', props.location.state, true, true);
          }} />
          <Route path="/guestForm" render={(props) => {
            return makeScreen('guestForm', props.location.state, true, true);
          }} />
          <Route path="/roll" render={(props) => {
            return makeScreen('roll', props.location.state, true, true);
          }} />
          <Route path="/first" render={(props) => {
            return makeScreen('first', props.location.state, true, true);
          }} />
          <Route path="/second" render={(props) => {
            return makeScreen('second', props.location.state, true, true);
          }} />
          <Route path="/start" render={(props) => {
            return makeScreen('start', props.location.state, true, true);
          }} />
        </Switch>
      </div>
    );
  }
}
export default withRouter(App)
