  sendData_state0_elButton_to_aquaroll = () => {
    const dataSheet = this.context.appActions.getDataSheet('aquaroll');
  
    let row = this.props.dataSheetRow || {
      "first": this.props.first,
      "last": this.props.last,
      "dept": this.props.dept,
    };
    row = { ...row, 
      last: this.props.dataSheetRow.last,
      first: this.props.dataSheetRow.first,
      dept: this.props.dataSheetRow.dept,
      datetime: (this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_date'] : ''),
      checkedin: (this.context.appActions.dataSlots ? this.context.appActions.dataSlots['ds_checked'] : ''),
    };
    if (this.props.dataSheetId === dataSheet.id) {
      return this.context.appActions.updateInDataSheet('aquaroll', row);
    } else {
      return this.context.appActions.addToDataSheet('aquaroll', row);
    }
  }
  
  
